// fonts
$default-fonts: Arial, sans-serif;
//=sans
//ont-family: 'Gotham Pro', $default-fonts

// colors
// $black: #000

@mixin trs {
	transition: all 0.25s; }


/* ===================================
	 Main Styles
	 @mixin ================================== */
body {
	position: relative;
	min-width: 1000px;
	font-family: 'Gotham Pro', $default-fonts;
	font-size: 20px;
	color: #262525;
	font-weight: 300;
	line-height: 1.5; }

.wrapper {
	position: relative;
	overflow: hidden;
	margin: 0 auto;
	min-height: 100%; }

.container {
	margin: 0 auto;
	padding: 0 10px;
	min-width: 1000px;
	max-width: 1620px;
	@include pie-clearfix; }


/* Helpers
	----------------------------------- */
button {
	background-color: transparent; }

a:hover, a:focus {
	text-decoration: none; }

a {
	color: inherit;
	text-decoration: none; }

table {
	vertical-align: top; }

img {
	max-width: 100%;
	vertical-align: top; }

.icon {
	display: inline-block;
	vertical-align: middle; }

.text-left {
	text-align: left; }

.text-center {
	text-align: center; }

.text-right {
	text-align: right; }

.is-hidden {
	display: none; }

.vertical {
	display: inline-block;
	vertical-align: middle; }

.cfx {
	@include pie-clearfix; }

// *
// 	+selection
// 		color: #fff
// 		background-color: $red

.btn {
	display: inline-block;
	&_blue {
		padding: 10px 29px 9px;
		font-family: 'Gotham Pro', $default-fonts;
		font-size: 16px;
		color: #fff;
		font-weight: 700;
		line-height: 29px;
		text-transform: uppercase;
		background-color: #e87b05;
		background-image: linear-gradient(to top, #3ba1e2 0%, #3ba1e2 1%, #6eb5da 99%, #6eb5da 100%);
		border: 1px solid #3f8eca;
		border-radius: 5px;
		@include trs;
		&:hover {
			background-image: linear-gradient(to top, #6eb5da 0%, #6eb5da 1%, #3ba1e2 99%, #3ba1e2 100%); }
		&:active {
			background-image: linear-gradient(to top, #58a3ca 0%, #58a3ca 1%, #3290cb 99%, #3290cb 100%); } }
	&_sm {
		padding: 11px 24px 8px; } }

/* ===================================
	 Header
	 @mixin ================================== */
.header {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	z-index: 100;
	padding-top: 15px;
	&__logo {
		float: left; }
	&__right {
		float: right; }
	&__row {
		@include pie-clearfix; }
	&__contact {
		float: right;
		margin-left: 90px;
		padding-top: 14px;
		font-size: 24px;
		color: #242424;
		line-height: 1.25;
		font-weight: 700;
		@include pie-clearfix;
		@include r(1199) {
			margin-left: 30px; }
		&-phone {
			float: left;
			& + & {
				margin-left: 60px;
				@include r(1199) {
					margin-left: 30px; } } } }
	&__list-img {
		float: right;
		font-size: 0; }
	&__list-image {
		display: inline-block;
		margin-left: 15px;
		vertical-align: middle; } }

.nav {
	float: right;
	font-size: 18px;
	line-height: 1.67;
	font-weight: 400;
	@include r(1199) {
		font-size: 16px;
		letter-spacing: -0.05em; }
	&__link {
		float: left;
		& + & {
			margin-left: 30px;
			@include r(1199) {
				margin-left: 12px; } } } }

.logo {
	display: block;
	font-family: 'Open Sans Condensed', $default-fonts;
	font-size: 57px;
	color: #2a3747;
	line-height: 1.33;
	font-weight: 700;
	text-transform: uppercase;
	&__link {
		&-letter {
			color: #f6821b; }
		&-text {
			display: block;
			margin-top: -12px;
			margin-left: 3px;
			font-family: 'Arial', $default-fonts;
			font-size: 14px;
			color: #2a3747;
			font-weight: 700;
			line-height: 1.14;
			text-transform: none;
			letter-spacing: 0.05em; } } }


/* ===================================
	 Footer
	 @mixin ================================== */
.footer {
	position: relative;
	z-index: 2;
	font-size: 18px;
	color: #ffffff;
	font-weight: 400;
	padding-top: 65px;
	background: url(../img/bg/comment.jpg) center center no-repeat;
	background-size: cover;
	&__container {
		position: relative;
		z-index: 3;
		margin-left: auto;
		margin-right: auto;
		max-width: 994px; }
	&:before {
		content: '';
		position: absolute;
		z-index: 2;
		pointer-events: none;
		top: 0;
		left: 0;
		bottom: 0;
		width: 50%;
		background: url(../img/bg/comment-left.png) center center no-repeat;
		background-size: cover; }
	&:after {
		content: '';
		position: absolute;
		z-index: 2;
		pointer-events: none;
		top: 0;
		right: 0;
		bottom: 0;
		width: 50%;
		background: url(../img/bg/comment-right.png) center center no-repeat;
		background-size: cover; }
	&__title {
		font-size: 30px;
		font-weight: 500;
		text-align: center; }
	&__subtitle {
		text-align: center; }
	&__row {
		@include pie-clearfix; }
	&__col {
		float: left;
		width: 33.33%;
		&:last-child {
			float: right;
			width: 257px; }
		&-title {
			margin-top: -17px;
			margin-bottom: 3px;
			font-size: 14px;
			font-weight: 500;
			line-height: 1.71; }
		&-subtitle {
			margin-bottom: 12px;
			font-size: 14px;
			font-weight: 400;
			line-height: 1.57; } }
	&__logo {
		margin-top: 10px;
		margin-bottom: 5px;
		font-size: 45px;
		color: #ffffff;
		.logo__link-text {
			margin-top: -8px;
			font-size: 10px;
			color: #ffffff;
			letter-spacing: 0; } }
	&__address {
		font-size: 18px;
		line-height: 1.67;
		font-weight: 400; }
	&__link {
		display: block;
		margin-top: 2px;
		font-size: 18px;
		color: #2f9ce7;
		text-decoration: underline;
		@include trs;
		&:hover {
			color: lighten(#2f9ce7,10);
			text-decoration: underline; } }
	&__nav {
		margin-top: 50px;
		padding-left: 80px;
		&-link {
			display: block;
			font-size: 18px;
			font-weight: 400;
			line-height: 1.67;
			@include trs;
			&:hover {
				color: #2f9ce7; } } }
	&__form {
		&-field {
			@include pie-clearfix;
			& + & {
				margin-top: 15px; } }
		&-input {
			padding-left: 15px;
			padding-right: 15px;
			width: 100%;
			height: 50px;
			font-size: 16px;
			color: inherit;
			line-height: 50px;
			border: 1px solid #b9b9b9;
			border-radius: 9px;
			background-color: transparent;
			@include placeholder-color(#c6c6c6); }
		&-submit {
			width: 100%;
			border-radius: 6px; } }
	&__bot {
		padding-top: 15px;
		padding-bottom: 15px;
		margin-top: 50px;
		font-size: 14px;
		font-weight: 400;
		border-top: 1px solid rgba(255,255,255,.39);
		@include pie-clearfix;
		&-left {
			float: left; }
		&-right {
			float: right; }
		&-link {
			color: #2f9ce7;
			@include trs;
			&:hover {
				color: lighten(#2f9ce7,20); } } } }


/* ===================================
	 Main
	 @mixin ================================== */
.main {}

/* ===================================
	 Start
	 @mixin ================================== */
.start {
	position: relative;
	padding-top: 150px;
	padding-bottom: 150px;
	background-color: #b6b6b6;
	background-image: linear-gradient(to top, #eef5fb 0%, #fafcfd 99%, #fafcfd 100%);
	&__col {
		float: left;
		width: 50%; }
	&__title {
		margin-top: 90px;
		font-size: 46px;
		line-height: 1.04;
		font-weight: 300;
		margin-bottom: 25px;
		&-line {
			position: relative;
			z-index: 1;
			&:before {
				content: '';
				position: absolute;
				left: 2px;
				right: 2px;
				bottom: -4px;
				height: 2px;
				background-color: #f6821b;
				z-index: 1; } } }
	&__subtitle {
		margin-bottom: 25px;
		max-width: 610px;
		color: #8f939b;
		font-weight: 400; }
	&__img {
		pointer-events: none;
		margin-top: -65px;
		max-width: none;
		width: calc(100% + 160px); } }

/* ===================================
	 Code
	 @mixin ================================== */
.code {
	padding-top: 35px;
	padding-bottom: 43px;
	&__title {
		margin-bottom: 10px;
		text-align: center;
		font-size: 24px;
		font-weight: 300; }
	&__form {
		text-align: center;
		font-size: 0;
		@include pie-clearfix;
		&-field {
			font-size: 16px;
			display: inline-block;
			width: 456px;
			& + & {
				margin-left: 18px;
				width: 230px; }
			& + & + & {
				width: auto; } }
		&-input {
			padding-left: 15px;
			padding-right: 15px;
			width: 100%;
			height: 50px;
			font-size: 16px;
			color: inherit;
			line-height: 50px;
			border: 1px solid #b9b9b9;
			border-radius: 9px;
			@include placeholder-color(#c6c6c6); }
		button[type='submit'] {
			border-radius: 6px; } } }

/* ===================================
	 Econom
	 @mixin ================================== */
.econom {
	padding-top: 40px;
	padding-bottom: 40px;
	background-color: #f9fbfd;
	&__title {
		margin-bottom: 35px;
		margin-left: auto;
		margin-right: auto;
		max-width: 600px;
		font-size: 30px;
		font-weight: 500;
		line-height: 1.2; }
	&__row {
		margin-left: auto;
		margin-right: auto;
		max-width: 1200px;
		@include pie-clearfix; }
	&__col {
		float: left;
		width: 33.3%;
		text-align: center; }
	&__col:nth-child(1) {
		padding-left: 0;
		padding-right: 25px; }
	&__col:nth-child(2) &__text {
		padding-left: 15px;
		padding-right: 15px; }
	&__col:nth-child(3) {
		padding-left: 40px;
		padding-right: 0; }
	&__icon {
		margin-bottom: 20px;
		font-size: 0;
		.icon {
			font-size: 81px; } }
	&__desc {
		text-transform: uppercase;
		font-size: 20px;
		font-weight: 700; } }

/* ===================================
	 Bonus
	 @mixin ================================== */
.bonus {
	padding-top: 55px;
	min-height: 480px;
	background: url(../img/bg/delivery.png) center bottom no-repeat;
	background-size: 100% auto;
	&__title {
		margin-bottom: 2px;
		font-size: 30px;
		font-weight: 500;
		text-align: center; }
	&__subtitle {
		margin-left: auto;
		margin-right: auto;
		max-width: 375px;
		text-align: center; } }

/* ===================================
	 Cert
	 @mixin ================================== */
.cert {
	padding-top: 60px;
	padding-bottom: 85px;
	color: #ffffff;
	background: url(../img/bg/cert.png) center center no-repeat;
	background-size: cover;
	&__title {
		margin-bottom: 2px;
		font-size: 30px;
		font-weight: 500;
		text-align: center; }
	&__subtitle {
		text-align: center; }
	&__list {
		margin-top: 40px;
		margin-left: auto;
		margin-right: auto;
		max-width: 1200px;
		&-item {
			@include pie-clearfix;
			& + & {
				margin-top: 80px; } }
		&-icon {
			float: left;
			margin-right: 16px;
			margin-top: 22px;
			width: 76px;
			height: 76px;
			border-radius: 50%;
			border: 1px solid #fff;
			@include vertical; }
		&-item &-icon {
			position: relative;
			&:after {
				content: '';
				position: absolute;
				left: 50%;
				top: 100%;
				margin-top: 3px;
				width: 1px;
				height: 120px;
				border-left: 1px dashed white; } }
		&-item:last-child &-icon {
			&:after {
				content: none;
				display: none; } }
		&-content {
			overflow: hidden;
			max-width: 380px; }
		&-title {
			font-size: 20px;
			font-weight: 700;
			text-transform: uppercase; } } }

/* ===================================
	 Discount
	 @mixin ================================== */
.discount {
	padding-top: 65px;
	padding-bottom: 65px;
	&__title {
		margin-bottom: 2px;
		text-align: center;
		font-size: 30px;
		font-weight: 700;
		&-mark {
			color: #f63939; } }
	&__subtitle {
		margin-bottom: 30px;
		margin-left: auto;
		margin-right: auto;
		max-width: 570px;
		text-align: center; }
	&__info {
		margin-top: 100px;
		margin-bottom: 20px;
		color: #f63939;
		text-align: center; }
	&__form {
		margin-left: auto;
		margin-right: auto;
		width: 295px;
		&-field {
			position: relative;
			z-index: 1;
			@include pie-clearfix;
			& + & {
				margin-top: 15px; } }
		&-select-style {
			position: relative;
			&:after {
				content: '';
				position: absolute;
				right: 15px;
				top: 23px;
				z-index: 1;
				@include arr(9,5,#000,b); } }
		&-select {
			appearance: none;
			padding: 0 38px 0 15px;
			width: 100%;
			height: 50px;
			font-size: 16px;
			line-height: 48px;
			border: 1px solid #bababa;
			border-radius: 6px;
			background-color: transparent; }
		&-input {
			padding-left: 15px;
			padding-right: 15px;
			width: 100%;
			height: 50px;
			font-size: 16px;
			color: inherit;
			line-height: 50px;
			border: 1px solid #bababa;
			border-radius: 6px;
			@include placeholder-color(#bababa); }
		button[type='submit'] {
			border-radius: 6px; }
		&-submit {
			width: 100%;
			padding-left: 0;
			padding-right: 0; } }
	&__form-info {
		margin-top: 15px;
		font-size: 16px;
		text-align: center; } }

/* ===================================
	 Person
	 @mixin ================================== */
.person {
	padding-top: 55px;
	padding-bottom: 75px;
	background-color: #f9fbfd;
	&__title {
		margin-bottom: 2px;
		font-size: 30px;
		font-weight: 500;
		text-align: center; }
	&__subtitle {
		margin-bottom: 20px;
		text-align: center; } }

/* Manager
	----------------------------------- */
.manager {
	display: table;
	margin-left: auto;
	margin-right: auto;
	width: 100%;
	max-width: 1200px;
	&__col {
		display: table-cell;
		vertical-align: top;
		&:nth-child(1),
		&:nth-child(3) {
			padding-top: 45px;
			padding-bottom: 50px;
			width: 350px;
			background-color: white;
			text-align: center;
			border-radius: 8px;
			box-shadow: 0 0 7px rgba(103, 156, 210, 0.06); } }
	&__img {
		margin-bottom: 10px; }
	&__name {
		margin-bottom: 3px;
		font-size: 24px;
		font-weight: 500; }
	&__phone {
		display: inline-block;
		vertical-align: middle;
		font-size: 22px;
		font-weight: 400;
		&-icon {
			display: inline-block;
			margin-right: 12px;
			vertical-align: middle;
			position: relative;
			top: -2px; } }
	&__mail {
		display: inline-block;
		vertical-align: middle;
		font-size: 18px;
		color: #2f9ce7;
		font-weight: 400;
		text-decoration: underline;
		&-icon {
			display: inline-block;
			margin-right: 12px;
			vertical-align: middle;
			position: relative;
			top: -2px; } }
	&__row {
		margin-bottom: 0;
		text-align: left;
		padding-left: 65px;
		& + & {
			margin-bottom: 15px; } }
	&__btn {
		padding-left: 0;
		padding-right: 0;
		width: 283px; }
	&__list {
		padding-left: 20px;
		padding-right: 30px;
		&-item {
			margin-top: 35px;
			@include pie-clearfix; }
		&-icon {
			margin-top: 3px;
			margin-right: 14px;
			font-size: 0;
			float: left;
			.icon {
				font-size: 31px; } }
		&-text {
			overflow: hidden; } } }


/* ===================================
	 FAQ
	 @mixin ================================== */
.faq {
	padding-top: 55px;
	padding-bottom: 90px;
	&__title {
		margin-bottom: 2px;
		font-size: 30px;
		font-weight: 500;
		text-align: center; }
	&__subtitle {
		margin-bottom: 35px;
		text-align: center; }
	&__row {
		max-width: 1200px;
		margin-left: auto;
		margin-right: auto;
		transition: all 1s;
		@include pie-clearfix; }
	&__list {
		margin-right: 40px;
		float: left;
		width: 420px;
		&-nav {
			position: relative;
			cursor: pointer;
			padding: 20px 8px 20px 18px;
			color: #383838;
			border: 1px solid #3f8eca;
			border-radius: 10px;
			background-color: #ffffff;
			@include trs;
			& + & {
				margin-top: 25px; }
			&:before {
				position: absolute;
				z-index: 0;
				left: 100%;
				top: 50%;
				margin-top: -9px;
				opacity: 0;
				@include arr(12,18,#5caedd,r);
				content: '';
				@include trs; }
			&:after {
				position: absolute;
				z-index: -1;
				left: 100%;
				top: 50%;
				margin-top: -10px;
				opacity: 0;
				@include arr(14,20,#3f8eca,r);
				content: '';
				@include trs; }
			&.is-active {
				z-index: 1;
				color: #ffffff;
				background-color: #5caedd;
				border: 1px solid #3f8eca;
				&:before,
				&:after {
