/* =================================== Main Styles @mixin ================================== */
body { position: relative; min-width: 1000px; font-family: "Gotham Pro", Arial, sans-serif; font-size: 20px; color: #262525; font-weight: 300; line-height: 1.5; }

.wrapper { position: relative; overflow: hidden; margin: 0 auto; min-height: 100%; }

.container { margin: 0 auto; padding: 0 10px; min-width: 1000px; max-width: 1620px; }

.container:after { content: ""; display: table; clear: both; }

/* Helpers ----------------------------------- */
button { background-color: transparent; }

a:hover, a:focus { text-decoration: none; }

a { color: inherit; text-decoration: none; }

table { vertical-align: top; }

img { max-width: 100%; vertical-align: top; }

.icon { display: inline-block; vertical-align: middle; }

.text-left { text-align: left; }

.text-center { text-align: center; }

.text-right { text-align: right; }

.is-hidden { display: none; }

.vertical { display: inline-block; vertical-align: middle; }

.cfx:after { content: ""; display: table; clear: both; }

.btn { display: inline-block; }

.btn_blue { padding: 10px 29px 9px; font-family: "Gotham Pro", Arial, sans-serif; font-size: 16px; color: #fff; font-weight: 700; line-height: 29px; text-transform: uppercase; background-color: #e87b05; background-image: linear-gradient(to top, #3ba1e2 0%, #3ba1e2 1%, #6eb5da 99%, #6eb5da 100%); border: 1px solid #3f8eca; border-radius: 5px; transition: all 0.25s; }

.btn_blue:hover { background-image: linear-gradient(to top, #6eb5da 0%, #6eb5da 1%, #3ba1e2 99%, #3ba1e2 100%); }

.btn_blue:active { background-image: linear-gradient(to top, #58a3ca 0%, #58a3ca 1%, #3290cb 99%, #3290cb 100%); }

.btn_sm { padding: 11px 24px 8px; }

/* =================================== Header @mixin ================================== */
.header { position: absolute; top: 0; left: 0; right: 0; z-index: 100; padding-top: 15px; }

.header__logo { float: left; }

.header__right { float: right; }

.header__row:after { content: ""; display: table; clear: both; }

.header__contact { float: right; margin-left: 90px; padding-top: 14px; font-size: 24px; color: #242424; line-height: 1.25; font-weight: 700; }

.header__contact:after { content: ""; display: table; clear: both; }

@media only screen and (max-width: 1199px) { .header__contact { margin-left: 30px; } }

.header__contact-phone { float: left; }

.header__contact-phone + .header__contact-phone { margin-left: 60px; }

@media only screen and (max-width: 1199px) { .header__contact-phone + .header__contact-phone { margin-left: 30px; } }

.header__list-img { float: right; font-size: 0; }

.header__list-image { display: inline-block; margin-left: 15px; vertical-align: middle; }

.nav { float: right; font-size: 18px; line-height: 1.67; font-weight: 400; }

@media only screen and (max-width: 1199px) { .nav { font-size: 16px; letter-spacing: -0.05em; } }

.nav__link { float: left; }

.nav__link + .nav__link { margin-left: 30px; }

@media only screen and (max-width: 1199px) { .nav__link + .nav__link { margin-left: 12px; } }

.logo { display: block; font-family: "Open Sans Condensed", Arial, sans-serif; font-size: 57px; color: #2a3747; line-height: 1.33; font-weight: 700; text-transform: uppercase; }

.logo__link-letter { color: #f6821b; }

.logo__link-text { display: block; margin-top: -12px; margin-left: 3px; font-family: "Arial", Arial, sans-serif; font-size: 14px; color: #2a3747; font-weight: 700; line-height: 1.14; text-transform: none; letter-spacing: 0.05em; }

/* =================================== Footer @mixin ================================== */
.footer { position: relative; z-index: 2; font-size: 18px; color: #ffffff; font-weight: 400; padding-top: 65px; background: url(../img/bg/comment.jpg) center center no-repeat; background-size: cover; }

.footer__container { position: relative; z-index: 3; margin-left: auto; margin-right: auto; max-width: 994px; }

.footer:before { content: ''; position: absolute; z-index: 2; pointer-events: none; top: 0; left: 0; bottom: 0; width: 50%; background: url(../img/bg/comment-left.png) center center no-repeat; background-size: cover; }

.footer:after { content: ''; position: absolute; z-index: 2; pointer-events: none; top: 0; right: 0; bottom: 0; width: 50%; background: url(../img/bg/comment-right.png) center center no-repeat; background-size: cover; }

.footer__title { font-size: 30px; font-weight: 500; text-align: center; }

.footer__subtitle { text-align: center; }

.footer__row:after { content: ""; display: table; clear: both; }

.footer__col { float: left; width: 33.33%; }

.footer__col:last-child { float: right; width: 257px; }

.footer__col-title { margin-top: -17px; margin-bottom: 3px; font-size: 14px; font-weight: 500; line-height: 1.71; }

.footer__col-subtitle { margin-bottom: 12px; font-size: 14px; font-weight: 400; line-height: 1.57; }

.footer__logo { margin-top: 10px; margin-bottom: 5px; font-size: 45px; color: #ffffff; }

.footer__logo .logo__link-text { margin-top: -8px; font-size: 10px; color: #ffffff; letter-spacing: 0; }

.footer__address { font-size: 18px; line-height: 1.67; font-weight: 400; }

.footer__link { display: block; margin-top: 2px; font-size: 18px; color: #2f9ce7; text-decoration: underline; transition: all 0.25s; }

.footer__link:hover { color: #5db2ec; text-decoration: underline; }

.footer__nav { margin-top: 50px; padding-left: 80px; }

.footer__nav-link { display: block; font-size: 18px; font-weight: 400; line-height: 1.67; transition: all 0.25s; }

.footer__nav-link:hover { color: #2f9ce7; }

.footer__form-field:after { content: ""; display: table; clear: both; }

.footer__form-field + .footer__form-field { margin-top: 15px; }

.footer__form-input { padding-left: 15px; padding-right: 15px; width: 100%; height: 50px; font-size: 16px; color: inherit; line-height: 50px; border: 1px solid #b9b9b9; border-radius: 9px; background-color: transparent; }

.footer__form-input:-moz-placeholder, .footer__form-input::-moz-placeholder { color: #c6c6c6; opacity: 1; }

.footer__form-input::-webkit-input-placeholder { color: #c6c6c6; }

.footer__form-submit { width: 100%; border-radius: 6px; }

.footer__bot { padding-top: 15px; padding-bottom: 15px; margin-top: 50px; font-size: 14px; font-weight: 400; border-top: 1px solid rgba(255, 255, 255, 0.39); }

.footer__bot:after { content: ""; display: table; clear: both; }

.footer__bot-left { float: left; }

.footer__bot-right { float: right; }

.footer__bot-link { color: #2f9ce7; transition: all 0.25s; }

.footer__bot-link:hover { color: #8ac8f2; }

/* =================================== Main @mixin ================================== */
/* =================================== Start @mixin ================================== */
.start { position: relative; padding-top: 150px; padding-bottom: 150px; background-color: #b6b6b6; background-image: linear-gradient(to top, #eef5fb 0%, #fafcfd 99%, #fafcfd 100%); }

.start__col { float: left; width: 50%; }

.start__title { margin-top: 90px; font-size: 46px; line-height: 1.04; font-weight: 300; margin-bottom: 25px; }

.start__title-line { position: relative; z-index: 1; }

.start__title-line:before { content: ''; position: absolute; left: 2px; right: 2px; bottom: -4px; height: 2px; background-color: #f6821b; z-index: 1; }

.start__subtitle { margin-bottom: 25px; max-width: 610px; color: #8f939b; font-weight: 400; }

.start__img { pointer-events: none; margin-top: -65px; max-width: none; width: calc(100% + 160px); }

/* =================================== Code @mixin ================================== */
.code { padding-top: 35px; padding-bottom: 43px; }

.code__title { margin-bottom: 10px; text-align: center; font-size: 24px; font-weight: 300; }

.code__form { text-align: center; font-size: 0; }

.code__form:after { content: ""; display: table; clear: both; }

.code__form-field { font-size: 16px; display: inline-block; width: 456px; }

.code__form-field + .code__form-field { margin-left: 18px; width: 230px; }

.code__form-field + .code__form-field + .code__form-field { width: auto; }

.code__form-input { padding-left: 15px; padding-right: 15px; width: 100%; height: 50px; font-size: 16px; color: inherit; line-height: 50px; border: 1px solid #b9b9b9; border-radius: 9px; }

.code__form-input:-moz-placeholder, .code__form-input::-moz-placeholder { color: #c6c6c6; opacity: 1; }

.code__form-input::-webkit-input-placeholder { color: #c6c6c6; }

.code__form button[type='submit'] { border-radius: 6px; }

/* =================================== Econom @mixin ================================== */
.econom { padding-top: 40px; padding-bottom: 40px; background-color: #f9fbfd; }

.econom__title { margin-bottom: 35px; margin-left: auto; margin-right: auto; max-width: 600px; font-size: 30px; font-weight: 500; line-height: 1.2; }

.econom__row { margin-left: auto; margin-right: auto; max-width: 1200px; }

.econom__row:after { content: ""; display: table; clear: both; }

.econom__col { float: left; width: 33.3%; text-align: center; }

.econom__col:nth-child(1) { padding-left: 0; padding-right: 25px; }

.econom__col:nth-child(2) .econom__text { padding-left: 15px; padding-right: 15px; }

.econom__col:nth-child(3) { padding-left: 40px; padding-right: 0; }

.econom__icon { margin-bottom: 20px; font-size: 0; }

.econom__icon .icon { font-size: 81px; }

.econom__desc { text-transform: uppercase; font-size: 20px; font-weight: 700; }

/* =================================== Bonus @mixin ================================== */
.bonus { padding-top: 55px; min-height: 480px; background: url(../img/bg/delivery.png) center bottom no-repeat; background-size: 100% auto; }

.bonus__title { margin-bottom: 2px; font-size: 30px; font-weight: 500; text-align: center; }

.bonus__subtitle { margin-left: auto; margin-right: auto; max-width: 375px; text-align: center; }

/* =================================== Cert @mixin ================================== */
.cert { padding-top: 60px; padding-bottom: 85px; color: #ffffff; background: url(../img/bg/cert.png) center center no-repeat; background-size: cover; }

.cert__title { margin-bottom: 2px; font-size: 30px; font-weight: 500; text-align: center; }

.cert__subtitle { text-align: center; }

.cert__list { margin-top: 40px; margin-left: auto; margin-right: auto; max-width: 1200px; }

.cert__list-item:after { content: ""; display: table; clear: both; }

.cert__list-item + .cert__list-item { margin-top: 80px; }

.cert__list-icon { float: left; margin-right: 16px; margin-top: 22px; width: 76px; height: 76px; border-radius: 50%; border: 1px solid #fff; font-size: 0; text-align: center; }

.cert__list-icon:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }

.cert__list-item .cert__list-icon { position: relative; }

.cert__list-item .cert__list-icon:after { content: ''; position: absolute; left: 50%; top: 100%; margin-top: 3px; width: 1px; height: 120px; border-left: 1px dashed white; }

.cert__list-item:last-child .cert__list-icon:after { content: none; display: none; }

.cert__list-content { overflow: hidden; max-width: 380px; }

.cert__list-title { font-size: 20px; font-weight: 700; text-transform: uppercase; }

/* =================================== Discount @mixin ================================== */
.discount { padding-top: 65px; padding-bottom: 65px; }

.discount__title { margin-bottom: 2px; text-align: center; font-size: 30px; font-weight: 700; }

.discount__title-mark { color: #f63939; }

.discount__subtitle { margin-bottom: 30px; margin-left: auto; margin-right: auto; max-width: 570px; text-align: center; }

.discount__info { margin-top: 100px; margin-bottom: 20px; color: #f63939; text-align: center; }

.discount__form { margin-left: auto; margin-right: auto; width: 295px; }

.discount__form-field { position: relative; z-index: 1; }

.discount__form-field:after { content: ""; display: table; clear: both; }

.discount__form-field + .discount__form-field { margin-top: 15px; }

.discount__form-select-style { position: relative; }

.discount__form-select-style:after { content: ''; position: absolute; right: 15px; top: 23px; z-index: 1; width: 0px; height: 0px; border-style: solid; border-width: 5px 4.5px 0 4.5px; border-color: #000 transparent transparent transparent; }

.discount__form-select { appearance: none; padding: 0 38px 0 15px; width: 100%; height: 50px; font-size: 16px; line-height: 48px; border: 1px solid #bababa; border-radius: 6px; background-color: transparent; }

.discount__form-input { padding-left: 15px; padding-right: 15px; width: 100%; height: 50px; font-size: 16px; color: inherit; line-height: 50px; border: 1px solid #bababa; border-radius: 6px; }

.discount__form-input:-moz-placeholder, .discount__form-input::-moz-placeholder { color: #bababa; opacity: 1; }

.discount__form-input::-webkit-input-placeholder { color: #bababa; }

.discount__form button[type='submit'] { border-radius: 6px; }

.discount__form-submit { width: 100%; padding-left: 0; padding-right: 0; }

.discount__form-info { margin-top: 15px; font-size: 16px; text-align: center; }

/* =================================== Person @mixin ================================== */
.person { padding-top: 55px; padding-bottom: 75px; background-color: #f9fbfd; }

.person__title { margin-bottom: 2px; font-size: 30px; font-weight: 500; text-align: center; }

.person__subtitle { margin-bottom: 20px; text-align: center; }

/* Manager ----------------------------------- */
.manager { display: table; margin-left: auto; margin-right: auto; width: 100%; max-width: 1200px; }

.manager__col { display: table-cell; vertical-align: top; }

.manager__col:nth-child(1), .manager__col:nth-child(3) { padding-top: 45px; padding-bottom: 50px; width: 350px; background-color: white; text-align: center; border-radius: 8px; box-shadow: 0 0 7px rgba(103, 156, 210, 0.06); }

.manager__img { margin-bottom: 10px; }

.manager__name { margin-bottom: 3px; font-size: 24px; font-weight: 500; }

.manager__phone { display: inline-block; vertical-align: middle; font-size: 22px; font-weight: 400; }

.manager__phone-icon { display: inline-block; margin-right: 12px; vertical-align: middle; position: relative; top: -2px; }

.manager__mail { display: inline-block; vertical-align: middle; font-size: 18px; color: #2f9ce7; font-weight: 400; text-decoration: underline; }

.manager__mail-icon { display: inline-block; margin-right: 12px; vertical-align: middle; position: relative; top: -2px; }

.manager__row { margin-bottom: 0; text-align: left; padding-left: 65px; }

.manager__row + .manager__row { margin-bottom: 15px; }

.manager__btn { padding-left: 0; padding-right: 0; width: 283px; }

.manager__list { padding-left: 20px; padding-right: 30px; }

.manager__list-item { margin-top: 35px; }

.manager__list-item:after { content: ""; display: table; clear: both; }

.manager__list-icon { margin-top: 3px; margin-right: 14px; font-size: 0; float: left; }

.manager__list-icon .icon { font-size: 31px; }

.manager__list-text { overflow: hidden; }

/* =================================== FAQ @mixin ================================== */
.faq { padding-top: 55px; padding-bottom: 90px; }

.faq__title { margin-bottom: 2px; font-size: 30px; font-weight: 500; text-align: center; }

.faq__subtitle { margin-bottom: 35px; text-align: center; }

.faq__row { max-width: 1200px; margin-left: auto; margin-right: auto; transition: all 1s; }

.faq__row:after { content: ""; display: table; clear: both; }

.faq__list { margin-right: 40px; float: left; width: 420px; }

.faq__list-nav { position: relative; cursor: pointer; padding: 20px 8px 20px 18px; color: #383838; border: 1px solid #3f8eca; border-radius: 10px; background-color: #ffffff; transition: all 0.25s; }

.faq__list-nav + .faq__list-nav { margin-top: 25px; }

.faq__list-nav:before { position: absolute; z-index: 0; left: 100%; top: 50%; margin-top: -9px; opacity: 0; width: 0px; height: 0px; border-style: solid; border-width: 9px 0 9px 12px; border-color: transparent transparent transparent #5caedd; content: ''; transition: all 0.25s; }

.faq__list-nav:after { position: absolute; z-index: -1; left: 100%; top: 50%; margin-top: -10px; opacity: 0; width: 0px; height: 0px; border-style: solid; border-width: 10px 0 10px 14px; border-color: transparent transparent transparent #3f8eca; content: ''; transition: all 0.25s; }

.faq__list-nav.is-active { z-index: 1; color: #ffffff; background-color: #5caedd; border: 1px solid #3f8eca; }

.faq__list-nav.is-active:before, .faq__list-nav.is-active:after { opacity: 1; }

.faq__content { overflow: hidden; }

.faq__content a { color: #2f9ce7; text-decoration: underline; transition: all 0.25s; }

.faq__content a:hover { color: #12659e; text-decoration: underline; }

.faq__content p { margin-bottom: 20px; }

.faq__content img { margin-bottom: 20px; }

.faq__content-row:after { content: ""; display: table; clear: both; }

.faq__content-col50 { float: left; width: 50%; }

.faq__content-col50 + .faq__content-col50 { padding-left: 40px; }

.faq__content-title { padding-left: 40px; font-size: 24px; font-weight: 700; }

.faq__content-icon { float: left; margin-right: 15px; margin-top: 3px; }

.faq__content-text { overflow: hidden; max-width: 270px; color: #383838; }

.faq__content-img-mod { margin-top: 50px; }

.faq__content-hor-row { color: #383838; }

.faq__content-hor-row:after { content: ""; display: table; clear: both; }

.faq__content-hor-row + .faq__content-hor-row { margin-top: 30px; }

.faq__content-hor-icon { margin-right: 10px; }

.faq__content b { font-weight: 700; }

/* =================================== Comment @mixin ================================== */
.comment { position: relative; z-index: 1; padding-top: 80px; padding-bottom: 105px; color: #ffffff; background: url(../img/bg/comment.jpg) center center no-repeat; background-size: cover; text-align: center; }

.comment__container { position: relative; z-index: 1; }

.comment:before { content: ''; position: absolute; z-index: 2; pointer-events: none; top: 0; left: 0; bottom: 0; width: 50%; background: url(../img/bg/comment-left.png) center center no-repeat; background-size: cover; }

.comment:after { content: ''; position: absolute; z-index: 2; pointer-events: none; top: 0; right: 0; bottom: 0; width: 50%; background: url(../img/bg/comment-right.png) center center no-repeat; background-size: cover; }

.comment__title { margin-bottom: 60px; font-size: 30px; font-weight: 500; }

.comment__row { margin-left: auto; margin-right: auto; font-size: 0; text-align: center; }

.comment__item { display: inline-block; width: 50%; padding-left: 85px; padding-right: 10px; max-width: 620px; }

.comment__text { position: relative; margin-bottom: 15px; font-size: 22px; line-height: 1.64; font-weight: 300; }

.comment__text:before { content: 'â€œ'; position: absolute; top: 0; margin-top: -30px; margin-right: -18px; right: 100%; opacity: 0.19; font-family: 'Gotham Pro'; font-size: 154px; font-weight: 900; line-height: 1; }

.comment__author { font-size: 18px; font-weight: 700; }

.comment__spec { margin-bottom: 5px; font-size: 14px; }

.comment__link { font-size: 20px; color: #3fa3e1; font-weight: 300; line-height: 1.8; text-decoration: underline; transition: all 0.25s; }

.comment__link:hover { color: #97cdef; text-decoration: underline; }

/* =================================== Guarant @mixin ================================== */
.guarant { padding-top: 75px; padding-bottom: 50px; background: url(../img/bg/guarant.png) bottom left repeat-x; }

.guarant__title { margin-bottom: 60px; font-size: 30px; font-weight: 500; text-align: center; }

.guarant__row { display: table; table-layout: fixed; margin-left: auto; margin-right: auto; width: 100%; max-width: 1220px; }

.guarant__col { width: 50%; display: table-cell; vertical-align: middle; }

.guarant__col:nth-child(2) { width: 400px; }

.guarant__item:after { content: ""; display: table; clear: both; }

.guarant__item + .guarant__item { margin-top: 75px; }

.guarant__icon { float: left; margin-right: 14px; }

.guarant__text { overflow: hidden; font-size: 20px; font-weight: 300; }

.guarant__text-link { color: #44a5e1; text-decoration: underline; transition: all 0.25s; }

.guarant__text-link:hover { color: #1a70a5; text-decoration: underline; }

.guarant__img { width: auto; max-width: none; height: auto; }

/* =================================== Navigation @mixin ================================== */
.navigation { position: absolute; bottom: 0; left: 0; right: 0; z-index: 2; }

.navigation__container { display: table; width: 100%; max-width: 1560px; }

.navigation__container:after { content: none; display: none; }

.navigation__btn { cursor: pointer; display: table-cell; vertical-align: middle; width: 25%; font-size: 16px; font-weight: 400; text-transform: uppercase; text-align: center; height: 140px; }

.navigation__btn + .navigation__btn { border-left: 1px solid #c8c8c8; }

/* =================================== El @mixin ================================== */
.el__slider { max-width: 1600px; position: relative; padding-top: 140px; }

.slider__item { position: relative; padding-left: 20px; padding-right: 20px; }

.slider__item-row { position: relative; z-index: 1; font-size: 0; }

.slider__abs { position: absolute; z-index: 2; left: 0; right: 0; bottom: 100%; margin-bottom: 15px; font-size: 14px; font-weight: 500; line-height: 1.43; text-transform: uppercase; }

.slider__num1 { position: relative; display: inline-block; margin-right: 5px; font-size: 18px; font-weight: 500; }

.slider__num1:before { pointer-events: none; content: ''; position: absolute; left: 0; top: -3px; bottom: 3px; right: 0; background: url(../img/bg/line.png) center center no-repeat; background-size: 100% 100%; }

.slider__text1 { display: inline-block; margin-right: 10px; font-size: 18px; font-weight: 500; }

.slider__text2 { display: inline-block; margin-right: 10px; font-size: 18px; font-weight: 500; }

.slider__num2 { display: inline-block; margin-right: 5px; color: #39444c; font-size: 24px; font-weight: 900; }

.slider__text3 { display: inline-block; margin-right: 5px; font-size: 18px; line-height: 20px; font-weight: 500; }

.slider__btn { display: block; height: 50px; font-size: 16px; color: #44a5e1; line-height: 50px; text-align: center; font-weight: 700; text-transform: uppercase; border: 1px solid #3f8eca; border-radius: 6px; }

.slider__btn:hover { color: #ffffff; background-image: linear-gradient(to top, #6eb5da 0%, #6eb5da 1%, #3ba1e2 99%, #3ba1e2 100%); border: 1px solid #3f8eca; }

.slider__btn:active { color: #ffffff; background-image: linear-gradient(to top, #58a3ca 0%, #58a3ca 1%, #3290cb 99%, #3290cb 100%); border: 1px solid #3f8eca; }

/* =================================== Other Styles @mixin ================================== */
